import { DO_LOG_IN, DO_LOG_OUT } from './type';

/**
 |--------------------------------------------------
 | Log in
 |--------------------------------------------------
 */

export function doLogIn() {
  console.log('login', true);
  return {
    type: DO_LOG_IN,
    payload: true
  };
}

export function doLogOut() {
  console.log('login', false);
  return {
    type: DO_LOG_OUT,
    payload: false
  };
}
